import React from "react";
import './form.css';
import "date-fns";
import {Modal,Button,Image} from 'react-bootstrap'
import { Link} from 'react-router-dom'
import modal from './modal.jpg'

class Form extends React.Component {
  state = {
    sDate: "",
    endD: ""
  };

constructor(){
  super();
  this.state={
    show:false
  }
}
handleModal (){
  this.setState({show:!this.state.show})
}
  
render(){  
  const { sDate } = this.state;
return (
  <>
    <div class="limiter">
      <div class="wrap-car-form">
        <div class = "container-car-form">
          <form className="form">
            <div class="form-row">
              <div class="form-group col-md-6">
                <label for="inputFirstName">First Name</label>
                <input type="text" class="form-control" id="inputFirstName" placeholder="First Name" required />
              </div>
              <div class="form-group col-md-6">
                <label for="Last Name">Last Name</label>
                <input type="text" class="form-control" id="Last Name" placeholder="Last Name" required/>
              </div>
            </div>
            <div class="form-group">
              <label for="inputPhoneNumer">Phone Number</label>
              <input type="tel"class="form-control" id="inputPhoneNumber" placeholder="+91"  maxLength = "10" />
            </div>
            <div className="date">
            <label htmlFor="sDate">Issue Date</label>
            <input
              type="date"
              onChange={e => this.setState({ sDate: e.target.value })}
            />
            {sDate && (
              <React.Fragment>
                <label htmlFor="sDate">Return Date:</label>
                <input
                  type="date"
                  min= {sDate}
                  onChange={e => this.setState({ endD: e.target.value })}
                />
              </React.Fragment>
            )}
          </div>
            <div class="form-row">
              <div class="form-group col-md-8">
              <p style={{paddingTop:"100px"}}><button type="submit" class="button red"><Link to='/' style={{fontSize:"30px",padding:" 10px 28px",color:"white"}}>Back</Link></button></p>
                </div>
              <div class="form-group col-md-4">
              <p style={{paddingTop:"100px"}}><button type="submit" id="button" onClick ={()=>{this.handleModal()}} class="button black"style={{fontSize:"30px",padding:" 10px 28px",color:"white"}} > Book</button></p>
            
            </div>
           </div>          
          </form>
        </div>
      </div>
    </div>
    <Modal show={this.state.show} onHide={()=>this.handleModal()} style={{width:"50%",color:"black",backgroundColor:"transparent"}} size="md">
      <Modal.Header closeButton>BOOKING SUCCESSFULL</Modal.Header>
      <Modal.Body>
      <div class = "row">
          <div class = "col-md-6"> 
           <Image src = {modal} style={{width:"100%"}}/>
          </div>
          <div class = "col-md-6">
            <span style={{fontSize:"18px"}}><b>Booking Confirmed!</b></span>
            <div class = "row">
              <span style={{fontSize:"15px"}}>You have Booked <b>AUDI A6</b> Enjoy Riding !</span>
            </div>
          </div>
      </div>
              
      </Modal.Body>
      <Modal.Footer>
        <Button>
          <Link to="/" style={{color:"white"}}>
            Continue
          </Link> 
        </Button>
      
      </Modal.Footer>
    </Modal>
    </>
);
}
}
export default Form