import React, { Component } from 'react';
import { connect } from 'react-redux'
import { Link} from 'react-router-dom'

 class Home extends Component{
    
    
    render(){
        let itemList = this.props.items.map(item=>{
            return(
                <div className="container" >
                <div className="card">
                <div className="row no-gutters ">
                    <div className="card-image">
                    <img src={item.img} alt=""></img>
                    </div>
                    <div className="col-md-8">
                    <div className="card-body">
                        <div class= "row">
                            <div class = "col">
                    <h5 className="card-title">{item.title}</h5>
                        </div>
                        <div class = "col">
                            <button className="btn red" disabled={item.bool}><Link to="/form"  style={{color:'white'}}> BOOK NOW</Link></button>
                        </div>
                        <div class = "col">
                            <button className="btn black" disabled={item.bool}><Link to="/carDetails" style={{color:'white'}} >CAR DETAILS</Link></button>
                        </div>
                         </div>
                        <div className = "row ">
                            <div class = "col align-self-end">
                            <i className="material-icons">edit</i><span className = "card-text">{item.desc}</span> 
                            </div>
                            <div class = "col align-self-end">
                            <i className="material-icons">person</i><span className = "card-text"> {item.desc2}</span> 
                            </div>
                            <div className="col align-self-end">
                            <p className="card-text"><b>Price: {item.price}Rs</b></p>                       
                            </div>
                        </div>
                        <div className="">
                    <span style={{color:"red",paddingLeft:"300px"}}><b>{item.text}</b></span>
                    </div>
                   
                    </div>
                   
                    </div>
                    
                                   </div>

                </div>
              
              </div>
            )
        })

        return(
            <div className="container">
                <h3 className="center">Cars For Rent</h3>
                <div className="box">
                    {itemList}
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state)=>{
    return {
      items: state.items
    }
  }

export default connect(mapStateToProps)(Home)